.ready {
  display: block;
  height: 100vh;
  width: 100%;
  color: #FFF;
  text-align: left;
  left: 0;
}
.ready > h1:first-child {
  position: absolute;
  bottom: 150px;
  left: 5vw;
  z-index: 1;
}
.ready ul {
  position: absolute;
  top: 5vh;
  left: 5vw;
  z-index: 1;
  list-style-type: none;
}
.ready ul li {
  margin: 20px 0;
  max-width: 150px;
}
.ready ul li:nth-of-type(even) {
  text-align: right;
  -webkit-animation: dist 1s forwards ease;
          animation: dist 1s forwards ease;
}
.ready ul li:nth-of-type(odd) {
  text-align: left;
  -webkit-animation: dist2 1s forwards ease;
          animation: dist2 1s forwards ease;
}
@-webkit-keyframes dist {
50% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
}
100% {
    -webkit-transform: translateX(-25px) translateY(10vh);
            transform: translateX(-25px) translateY(10vh);
}
}
@keyframes dist {
50% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
}
100% {
    -webkit-transform: translateX(-25px) translateY(10vh);
            transform: translateX(-25px) translateY(10vh);
}
}
@-webkit-keyframes dist2 {
50% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
}
100% {
    -webkit-transform: translateX(175px) translateY(20vh);
