}
.chevron {
  position: absolute;
  left: 0;
  top: 0;
  height: 35px;
  margin: calc(50vh - 17.5px) 1vw;
  text-align: center;
  -webkit-transition: 0.8s ease;
  transition: 0.8s ease;
}
.chevron h1 {
  margin: auto 0;
}
.chevron:hover {
  cursor: pointer;
}
.flip {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}
.desc {
  position: absolute;
  left: 50px;
  width: calc(50vw - 100px);
  padding: 25px;
}
